// 'use strict'

let cart = {};

let d = document;

let products = d.querySelector('.products');

// console.dir(products);

// addCart - –¥–æ–±–∞–≤–∏—Ç—å —Ç–æ–≤–∞—Ä –≤ –∫–æ—Ä–∑–∏–Ω—É
function addCart(id, price){
    if(!cart.hasOwnProperty(id)){
        cart[id] = {};
        cart[id]['price'] = +price;
        cart[id]['count'] = 1;
        cart[id]['totalPrice'] =  cart[id]['count']*cart[id]['price'];
        console.log(cart);
    }
    else{
        cart[id]['count'] += 1;
        cart[id]['totalPrice'] =  cart[id]['count']*cart[id]['price'];
        console.log(cart);
    }
}
// delCart - —É–¥–∞–ª–∏—Ç—å —Ç–æ–≤–∞—Ä –∏–∑ –∫–æ—Ä–∑–∏–Ω—ã
function delCart(id){
    if(cart.hasOwnProperty(id)){
        if(cart[id]['count'] - 1 <= 0){
            delete cart[id]
            return;
        }
        cart[id]['count'] -= 1;
        cart[id]['totalPrice'] =  cart[id]['count']*cart[id]['price'];
        console.log(cart);
    }
    else{
        return;
    }
}
// delAllCart - —É–¥–∞–ª–∏—Ç—å –≤—Å–µ —Ç–æ–≤–∞—Ä—ã –∏–∑ –∫–æ—Ä–∑–∏–Ω—ã

// openCart  - –ø–æ–∫–∞–∑–∞—Ç—å —Å–æ–¥–µ—Ä–∂–∏–º–æ–µ –∫–æ—Ä–∑–∏–Ω—ã
let btns = d.querySelectorAll('.product__btn');

// –í–µ—à–∞–µ–º —Å–æ–±—ã—Ç–∏—è –Ω–∞ –≤—Å–µ –∫–Ω–æ–ø–∫–∏

// btns.forEach(btn => btn.addEventListener('click', (e) => {
//     let isClass = e.target.classList;
//     if (isClass.contains('product__btn--plus')) {
//         console.log(e.target);
//     } else if (isClass.contains('product__btn--minus')) {
//         console.log(e.target);
//     }; 
// }));

products.addEventListener('click', (e) => {
    let isClass = e.target.classList;
    let dataset = e.target.dataset;
    if (isClass.contains('product__btn--plus')) {
        addCart(dataset.id, dataset.price);
    } else if (isClass.contains('product__btn--minus')) {
        delCart(dataset.id);
    };
});
